/* Author: Kang Wang
 * Version: 1
 * Desc: Timesheet Detail CSS
 */

/* body css */
.container {
  /* box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.1), 0px 4px 5px 0px rgba(0,0,0,0.1), 0px 1px 10px 0px rgba(0,0,0,0.1); */
  background-color: white;
  width: 1000px;
  padding: 30px 0px 45px 0px;
  border: solid 1px lightgray;
  border-radius: 5px;
}
.timesheetTitle {
  font-size: medium;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* first row  */
.attributeRow {
  /* margin-left: 4%; */
  width: 93%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* employee name */
.empNumContainer {
  display: flex;
}
/* employee Number Title */
.empNumTitle {
  font-weight: bold;
}
/* Employee number */
.empNum {
  margin-left: 10px;
  float: left;
}
/* week number container */
.weekNumContainer {
  display: flex;
}
/* week number title */
.weekNumTitle {
  font-weight: bold;
  display: inline-block;
}
/* week number */
.weekNum {
  margin-left: 10px;
}
/* week ending container */
.weekEndContainer {
  display: flex;
}
/* week ending title */
.weekEndTitle {
  font-weight: bold;
}
/* week ending */
.weekEnd {
    margin-left: 10px;
}
/* second row */
.empNameAttribute {
  width: 93%;
  display: flex;
}
/* employee name title */
.empNameTitle {
  font-weight: bold;
}
/* employee name */
.empName {
  margin-left: 10px;
}

/* css for project code dropdown */
.projCode{
  width: 8rem;
}
/* css for wp id dropdown */
.wpId{
  width: 4rem;
}
